}

.menuToggle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    padding: 0 23px;
    border-bottom: 2px solid  rgba(12, 107, 107, 0.62);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
}
.menuToggle::before{
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background:rgba(12, 107, 107, 0.62);
    transform: translateY(-8px);
    transition: 0.5s;
}
.menuToggle_dark{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    padding: 0 23px;
    border-bottom: 2px solid  rgba(9, 19, 19, 0.62);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
}
.menuToggle_dark::before{
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background:rgba(9, 19, 19, 0.62);
    transform: translateY(-8px);
    transition: 0.5s;
}
.main_active{
    position: fixed;
    inset: 40px 0 40px 20px;
    background: rgba(12, 107, 107, 0.62);
    border-radius: 5px;
    width: 195px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 6px;
    /*box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;*/
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    /*box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;*/
    /*box-shadow: rgba(0, 0, 0, 0.38) 0px 54px 55px, rgba(0, 0, 0, 0.32) 0px -12px 30px, rgba(0, 0, 0, 0.38) 0px 4px 6px, rgba(0, 0, 0, 0.29) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;*/
    border-left: 1px solid rgba(255, 255, 255, 0.82);
    border-right: 1px solid rgba(255, 255, 255, 0.82);
    transition: 0.5s;

}
.navigation_active{
    /*color: red;*/
    position: fixed;
    inset: 0 10px 0 20px;
    background: url("https://64.media.tumblr.com/c482190848bfa287b3ca15b485e63fd0/c8b42d1221f34ad2-54/s400x600/25b997b8630c10ba966080a26aec6b38a8021781.gif") no-repeat;
    background-size: cover;
    /*background: rgba(213, 239, 231, 0.7);*/
    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    border-radius: 5px;
    width: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
}

.navigation_active .menuToggle::before{
    transform: translateY(0) rotate(45deg);
}
.menuToggle::after{
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background: rgba(12, 107, 107, 0.62);
    transform: translateY(8px);
    box-shadow: 0 -8px 0  rgba(12, 107, 107, 0.62);
    transition: 0.5s;

}
.navigation_active .menuToggle::after{
    transform: translateY(0) rotate(-45deg);
    box-shadow: 0 0 0  rgba(12, 107, 107, 0.62);
}

.navigation_active .menuToggle_dark::before{
    transform: translateY(0) rotate(45deg);
}
.menuToggle_dark::after{
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background: rgba(9, 19, 19, 0.62);
    transform: translateY(8px);
    box-shadow: 0 -8px 0  rgba(9, 19, 19, 0.62);
    transition: 0.5s;

}
.navigation_active .menuToggle_dark::after{
    transform: translateY(0) rotate(-45deg);
    box-shadow: 0 0 0  rgba(9, 19, 19, 0.62);
}

