.navigation  .list::before{
    content: '';
    position: absolute;
    top: -10px;
    left: 24px;
    width: 15px;
    height: 10px;
    background: transparent;
    border-bottom-right-radius: 20px;
    /*box-shadow: 6px 5px 0 5px var(--bg);*/
    box-shadow: 6px 3px 0 3px rgb(16, 136, 136);
    transform: scale(0);
    transform-origin: bottom right;
    transition: 0.5s;

}
.navigation  .list_active::before{
    right: 2px;
    transform: scale(1);
}
.navigation_active  .list::before{
